import * as React from 'react';
import { Controller, environment } from '@ali/4ever-cangjie';
import { HistoryRole } from '../../../utils/role';
import { IToolbarButtonConfig, RedoButton as Button } from '@ali/4ever-component';
import { redo } from '../../actions';

const SHORTCUTKEY = environment.IS_MAC ? 'âŒ˜+Shift+Z' : 'Ctrl+Shift+Z / Ctrl+Y';

export interface RedoButtonProps extends IToolbarButtonConfig {
  controller: Controller;
  locale?: Record<string, string>;
}

export default function RedoButton(props: RedoButtonProps) {
  const { controller, locale, disabled, tooltip: tooltipProp, ...rest } = props;
  const isDisabled = disabled || !controller.query('canRedo');
  const handleClick = React.useCallback(() => controller.run('onAction', redo()), [controller]);

  const tooltip = React.useMemo(() => ({
    title: locale?.redo || 'Redo',
    shortCut: SHORTCUTKEY,
    ...tooltipProp,
  }), [tooltipProp]);

  return (
    <Button
      testid="bi-toolbar-redo"
      disabled={isDisabled}
      onClick={handleClick}
      role={HistoryRole.redo}
      title={locale?.redo || 'Redo'}
      tooltip={tooltip}
      {...rest}
    />
  );
}
